<template>
  <div style="margin-bottom:20px">
    <a-input-search placeholder="input search text" style="width: 200px" @search="onSearch"/>
    <a-button type="primary" @click="showDrawer(false)">添加用户</a-button>
  </div>
    <a-table rowKey="id" :columns="columns" :data-source="userList" bordered :pagination="false">
    <template #state="{text}">
       <a-switch @click="stateChangeHandler(text.id,!text.mg_state)" :checked="text.mg_state" />
    </template>
    <!-- 操作插槽 -->
    <template #get="{text}">
      <!-- 编辑 -->
        <a-tooltip placement="top">
        <template #title>
        <span>编辑用户</span>
        </template>
       <a-button  @click="showDrawer(text.id)" type="primary" shape="circle">
        <template #icon><FormOutlined /></template>
        </a-button>
          </a-tooltip>

        <!-- 设置 -->
        <a-tooltip placement="top">
        <template #title>
        <span>设置用户</span>
        </template>
        <a-button type="primary" shape="circle" @click="roleBoxShow(text.id,text.username,text.role_name)">
        <template #icon><SettingOutlined /></template>
        </a-button>
          </a-tooltip>

        <!-- 删除 -->
          <a-tooltip placement="top">
        <template #title>
        <span>删除用户</span>
        </template>
        <a-button @click="removeUserHandler(text.id)" type="primary" shape="circle">
        <template #icon><CloseCircleOutlined /></template>
        </a-button>
          </a-tooltip>

    </template>
  </a-table>
  <!-- 分配角色弹窗 -->
      <a-modal v-model:visible="roleBoxVisible" title="分配角色" @ok="setRoleHandler">
      <p>当前用户: {{currentUserInfo.username}}</p>
      <p>当前角色: {{currentUserInfo.role_name}}</p>
      <p>当前用户:
          <a-select
            v-model:value="selectedValue"
            style="width: 120px"
          >
            <a-select-option :value="item.id" v-for="item in rolesList" :key="item.id">{{item.roleName}}</a-select-option>
          </a-select>
      </p>
    </a-modal>
  <!-- 分页器 -->
    <a-config-provider :locale="locale">
      <a-pagination @change="pageChangeHandler" @showSizeChange="showSizeChangeHandler" :defaultPageSize="2" :pageSizeOptions="['1','2','5','10']" :total="total" show-size-changer show-quick-jumper />
    </a-config-provider>
    <!-- 添加用户的表单容器 -->
    <a-drawer
    title="创建新用户"
    :width="720"
    :visible="visible"
    :body-style="{ paddingBottom: '80px' }"
    @close="onClose"
  >
    <a-form ref="formRef" :model="form" :rules="rules" layout="vertical">
      <a-row :gutter="16">
        <a-col :span="24">
          <a-form-item label="用户名" name="username">
            <a-input :disabled="isEditUser" v-model:value="form.username" placeholder="请输入用户名" />
          </a-form-item>
        </a-col>
      </a-row>
      <a-row :gutter="16">
        <a-col :span="18">
          <a-form-item v-if="!isEditUser" label="密码" name="password">
            <a-input v-model:value="form.password" placeholder="请输入密码" />
          </a-form-item>
        </a-col>
      </a-row>
       <a-row :gutter="16">
        <a-col :span="24">
          <a-form-item label="邮箱" name="email">
            <a-input v-model:value="form.email" placeholder="请输入邮箱" />
          </a-form-item>
        </a-col>
      </a-row>
      <a-row :gutter="16">
        <a-col :span="24">
          <a-form-item label="手机号" name="mobile">
            <a-input v-model:value="form.mobile" placeholder="请输入手机号" />
          </a-form-item>
        </a-col>
      </a-row>
    </a-form>
    <div
      :style="{
        position: 'absolute',
        right: 0,
        bottom: 0,
        width: '100%',
        borderTop: '1px solid #e9e9e9',
        padding: '10px 16px',
        background: '#fff',
        textAlign: 'right',
        zIndex: 1,
      }"
    >
      <a-button style="margin-right: 8px" @click="onClose">取消</a-button>
      <a-button type="primary" @click="submit">提交</a-button>
    </div>
  </a-drawer>
</template>

<script>
import { message } from 'ant-design-vue'
import zhCN from 'ant-design-vue/es/locale/zh_CN'
import { getCurrentInstance, onMounted, reactive, ref, toRaw } from 'vue'
import { FormOutlined, CloseCircleOutlined, SettingOutlined } from '@ant-design/icons-vue'
export default {
  components: {
    FormOutlined,
    CloseCircleOutlined,
    SettingOutlined
  },
  setup () {
    // 重置表单ref
    const formRef = ref()
    const { proxy } = getCurrentInstance()
    const columns = [
      {
        title: 'ID',
        dataIndex: 'id'

      },
      {
        title: '用户名',
        className: 'column-money',
        dataIndex: 'username'
      },
      {
        title: '邮箱',
        dataIndex: 'email'
      },
      {
        title: '电话',
        dataIndex: 'mobile'
      },
      {
        title: '角色',
        dataIndex: 'role_name'
      },
      {
        title: '状态',
        slots: { customRender: 'state' }

      },
      {
        title: '操作',
        slots: { customRender: 'get' }
      }
    ]

    // 定义axios请求函数
    const queryParams = {
      query: '',
      pagenum: 1,
      pagesize: 2
    }
    const onSearch = (val) => {
      queryParams.query = val
      queryData()
    }
    const userList = ref()
    const total = ref()
    // 重新请求页面数据
    const queryData = async () => {
      const { data } = await proxy.$http.get('users', {
        params: queryParams
      })
      total.value = data.total
      userList.value = data.users
    }
    onMounted(() => {
      queryData()
    })
    // 定义页码改变函数
    const pageChangeHandler = (page, pagesize) => {
      queryParams.pagenum = page
      queryParams.pagesize = pagesize
      queryData()
    }
    // 定义页码尺寸更改的触发函数
    const showSizeChangeHandler = (current, size) => {
      queryParams.pagenum = current
      queryParams.pagesize = size
      queryData()
    }
    // 实现抽屉显示的方法
    const visible = ref(false)
    //  如果idEditUser是true的话就是编辑用户,否则是添加用户
    const isEditUser = ref(false)
    let isEditUserID = ''
    const showDrawer = async (userID) => {
      if (userID) {
        isEditUserID = userID
        isEditUser.value = true
        const { meta, data } = await proxy.$http.get('users/' + userID)
        if (meta.status != 200) return message.error(meta.msg)
        form.username = data.username
        form.email = data.email
        form.mobile = data.mobile
      } else {
        isEditUser.value = false
        form.username = ''
        form.email = ''
        form.mobile = ''
      }
      visible.value = true
    }
    //  取消表单
    const onClose = () => {
      visible.value = false
      message.info('已取消')
      formRef.value.resetFields()
    }
    //  表单是数据 与 验证规则
    const form = reactive({
      username: '',
      password: '',
      email: '',
      mobile: ''
    })
    const rules = {
      username: [
        {
          required: true,
          message: '请输入用户名',
          trigger: 'blur'
        },
        { min: 2, max: 10, message: '用户名为2~10位字符', trigger: 'blur' }

      ],
      password: [
        {
          required: true,
          message: '请输入密码',
          trigger: 'blur'
        },
        { min: 6, max: 18, message: '密码为6~18位字符', trigger: 'blur' }

      ]
    }
    // 用isEditUser来判断是注册用户还是修改用户   然后提交到数据库
    const submit = async () => {
      if (isEditUser.value) {
        const params = {
          email: form.email,
          mobile: form.mobile
        }
        const { meta } = await proxy.$http.put('users/' + isEditUserID, params)
        if (meta.status != 200) return message.error(meta.msg)
        message.success(meta.msg)
      } else {
        const params = toRaw(form)
        const { meta } = await proxy.$http.post('users', params)
        if (meta.status != 201) return message.error(meta.msg)
        message.success(meta.msg)
      }
      queryData()
      visible.value = false
      formRef.value.resetFields()
    }
    // 更改用户数据
    const stateChangeHandler = async (userId, state) => {
      const { meta } = await proxy.$http.put(`users/${userId}/state/${state}`)
      if (meta.state == 200) return message.error(meta.msg)
      message.success(meta.msg)
      queryData()
    }
    // 删除用户
    const removeUserHandler = async (userID) => {
      const { meta } = await proxy.$http.delete('users/' + userID)
      if (meta.status != 200) return message.error(meta.msg)
      message.success(meta.msg)
      queryData()
    }
    // 保存所有角色的数据列表
    const rolesList = ref([])
    // 定义selete初始选择的状态
    const selectedValue = ref('请选择')
    // 定义分配角色弹窗盒子的显示与隐藏
    const roleBoxVisible = ref(false)
    //  弹窗里面的信息
    const currentUserInfo = reactive({})
    //  显示分配角色的对话框
    const roleBoxShow = async (id, username, rolename) => {
      currentUserInfo.id = id
      currentUserInfo.username = username
      currentUserInfo.role_name = rolename
      // 获取所有角色列表的请求
      const { meta, data } = await proxy.$http.get('roles')
      if (meta.status == 200) {
        rolesList.value = data
      }
      roleBoxVisible.value = true
    }
    // 提交用户角色的处理函数
    const setRoleHandler = async () => {
      if (selectedValue.value == '请选择') return message.info('请选择一个角色')
      const { meta } = await proxy.$http.put(`users/${currentUserInfo.id}/role`, {
        rid: selectedValue.value
      })
      if (meta.status != 200) return message.error(meta.msg)
      message.success(meta.msg)
      queryData()
      roleBoxVisible.value = false
      selectedValue.value = '请选择'
    }
    return {
      removeUserHandler,
      stateChangeHandler,
      setRoleHandler,
      roleBoxShow,
      roleBoxVisible,
      submit,
      rolesList,
      form,
      formRef,
      rules,
      selectedValue,
      onClose,
      visible,
      showDrawer,
      onSearch,
      showSizeChangeHandler,
      userList,
      columns,
      locale: zhCN,
      pageChangeHandler,
      total,
      isEditUser,
      currentUserInfo
    }
  }
}
</script>

<style lang="less" scoped>
.ant-btn{
  margin-left: 20px;
}
.ant-pagination{
  margin-top: 30px !important;
}
</style>
